import { API_BASE_URL } from '../../../common/constants/constants';
import { http } from '../../services';

export interface IQuiz {
  profiles?: 49 | 500 | 1000 | 3000 | 3001;
  api?: boolean;
  from?: string;
  survey?: string;
  teamMembers?: 1 | 5 | 10 | 20 | 21;
}
export async function sendQuizInfo(quiz: IQuiz) {

  const requestBody = JSON.stringify(quiz);

  return http(`${API_BASE_URL}/user/quiz`, {
    method: 'PATCH',
    body: requestBody,
  });
}

export const shwoQuizPage = async () => {
  const request = await http(`${API_BASE_URL}/user/target`);
  return request.body;
}
