import { Input, message } from 'antd';
import React, { FC, memo, useState } from 'react';
import { useTranslation } from 'react-i18next';

import { Back, SubmitButton } from '../StyledComponents';
import { TextArea } from './styles';

interface IManualTextInputForm {
  placeholder: string;
  return: () => any;
  onSubmit: (text: string) => any;
}
const ManualTextInputForm: FC<IManualTextInputForm> = (props) => {
  const [text, setText] = useState('');

  const { t: translation } = useTranslation();

  const onSubmit = () => {
    if (!text) {
      message.error('Please, enter text');

      return;
    }

    props.onSubmit(text);
  };

  return (
    <>
      <Back onClick={props.return} />

      <TextArea
        autoFocus={true}
        value={text}
        onChange={(e) => setText(e.target.value)}
        placeholder={props.placeholder}
        rows={4}
        autoSize={false}
        maxLength={1000}
      />

      <SubmitButton onClick={onSubmit}>{translation('interview.firstScreen.next')}</SubmitButton>
    </>
  );
};

export default memo(ManualTextInputForm);
