import React, { FC, memo } from 'react';
import { useTranslation } from 'react-i18next';

import { Subtitle, Title, SelectorContainer, Selector } from '../StyledComponents';

interface IAccounts {
  onSubmit: (number: 49|500|1000|3000|3001) => any;
}
const Accounts: FC<IAccounts> = (props) => {
  const { t: translation } = useTranslation();

  return (
    <>
      <Subtitle>
        {translation('interview.thirdScreen.title')}
        <br />
        {translation('interview.thirdScreen.titlePart2')}
      </Subtitle>
      <Title>
        {translation('interview.thirdScreen.subtitle')}
      </Title>

      <SelectorContainer>
        <Selector title={translation('interview.thirdScreen.firstAnswer.title')} onClick={() => props.onSubmit(3001)} />
        <Selector title={translation('interview.thirdScreen.secondAnswer.title')} onClick={() => props.onSubmit(3000)} />
        <Selector title={translation('interview.thirdScreen.thirdAnswer.title')} onClick={() => props.onSubmit(1000)} />
        <Selector title={translation('interview.thirdScreen.fourAnswer.title')} onClick={() => props.onSubmit(500)} />
        <Selector title={translation('interview.thirdScreen.fiveAnswer.title')} onClick={() => props.onSubmit(49)} />
      </SelectorContainer>
    </>
  )
};

export default memo(Accounts);
