import React, { FC, memo, useState } from 'react';
import { useTranslation } from 'react-i18next';

import { Subtitle, Title, Selector, SelectorContainer } from '../StyledComponents';
import ManualTextInputForm from './ManualTextInputForm';

interface IPurposes {
  onSubmit: (text: string) => any;
}
const Purposes: FC<IPurposes> = (props) => {
  const [isManualFormOpened, setIsManualFormOpened] = useState(false);

  const { t: translation } = useTranslation();

  const answers = [
    {
      label: `${translation('interview.firstScreen.firstAnswer.title')}`,
      text: `${translation('interview.firstScreen.firstAnswer.subtitle')}`,
      key: 'Gambling & Betting',
    }, {
      label: `${translation('interview.firstScreen.secondAnswer.title')}`,
      text: `${translation('interview.firstScreen.secondAnswer.subtitle')}`,
      key: 'Crypto',
    }, {
      label: `${translation('interview.firstScreen.thirdAnswer.title')}`,
      text: `${translation('interview.firstScreen.thirdAnswer.subtitle')}`,
      key: 'Crowd Marketing, SMM & SEO',
    },
    {
      label: `${translation('interview.firstScreen.fourAnswer.title')}`,
      text: `${translation('interview.firstScreen.fourAnswer.subtitle')}`,
      key: 'Affiliate Marketing',
    }, {
      label: `${translation('interview.firstScreen.fiveAnswer.title')}`,
      text: `${translation('interview.firstScreen.fiveAnswer.subtitle')}`,
      key: 'Dropshipping',
    },
  ];

  const renderContent = () => {
    if (isManualFormOpened) {
      return (
        <ManualTextInputForm
          placeholder={translation('interview.subtitle')}
          onSubmit={(text) => props.onSubmit(text)}
          return={() => setIsManualFormOpened(false)}
        />
      );
    }

    return (
      <>
        {answers.map(answer => (
          <Selector key={answer.label} title={answer.text} label={answer.label} onClick={() => props.onSubmit(answer.key)} />
        ))}
        <Selector title={translation('interview.firstScreen.sixAnswer.title')} onClick={() => setIsManualFormOpened(true)} />
      </>
    );
  };

  return (
    <>
      <Subtitle>
        {translation('interview.title')}
      </Subtitle>
      <Title>
        {translation('interview.subtitle')}
      </Title>

      <SelectorContainer>
        {renderContent()}
      </SelectorContainer>
    </>
  );
};

export default memo(Purposes);
