import React, { FC, memo } from 'react';
import { useTranslation } from 'react-i18next';

import { Subtitle, Title, SelectorContainer, Selector } from '../StyledComponents';

interface ITeam {
  onSubmit: (number: 1|5|10|20|21) => any;
}
const Team: FC<ITeam> = (props) => {
  const { t: translation } = useTranslation();

  return (
    <>
      <Subtitle>
        {translation('interview.secondScreen.title')}
        <br />
        {translation('interview.secondScreen.titlePart2')}
      </Subtitle>
      <Title>
        {translation('interview.secondScreen.subtitle')}
      </Title>

      <SelectorContainer>
        <Selector title={translation('interview.secondScreen.firstAnswer.title')} onClick={() => props.onSubmit(5)} />
        <Selector title={translation('interview.secondScreen.secondAnswer.title')} onClick={() => props.onSubmit(10)} />
        <Selector title={translation('interview.secondScreen.thirdAnswer.title')} onClick={() => props.onSubmit(20)} />
        <Selector title={translation('interview.secondScreen.fourAnswer.title')} onClick={() => props.onSubmit(21)} />
        <Selector title={translation('interview.secondScreen.fiveAnswer.title')} onClick={() => props.onSubmit(1)} />
      </SelectorContainer>
    </>
  );
};

export default memo(Team);
