import React, { FC, memo } from 'react';
import styled from '@emotion/styled';
import ChevronRightIcon from '@material-ui/icons/ChevronRight';

const Container = styled.div`
  padding: 24px;
  background-color: #0CBA97;
  border-radius: 12px;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: space-between;
  width: 100%;
  cursor: pointer;
`;

const TextContainer = styled.div`
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  gap: 8px;
`;

const Label = styled.span`
  color: #DEF7E9;
  flex: none;
  order: 0;
  flex-grow: 0;
  letter-spacing: 0.35px;
  line-height: 24px;
  font-size: 16px;
  font-weight: 400;
`;

const Title = styled.span`
  flex: none;
  order: 1;
  flex-grow: 0;
  color: #FFFFFF;
  letter-spacing: 0.15px;
  line-height: 28px;
  font-size: 20px;
  font-weight: 500;
`;

interface ISelector {
  title: string;
  label?: string;
  onClick: () => any;
}
const SelectorComponent: FC<ISelector> = (props) => {

  return (
    <>
      <Container onClick={props.onClick}>
        <TextContainer>
          {props.label && (
            <Label>{props.label}</Label>
          )}

          <Title>{props.title}</Title>
        </TextContainer>

        <ChevronRightIcon style={{ color: '#FFFFFF' }} />
      </Container>
    </>
  );
}

export const Selector = memo(SelectorComponent);
