import React, { FC, memo } from 'react';
import styled from '@emotion/styled';
import ChevronLeftIcon from '@material-ui/icons/ChevronLeft';
import { useTranslation } from 'react-i18next';

const Container = styled.div`
  padding: 24px;
  background-color: #0CBA97;
  border-radius: 12px;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: start;
  gap: 16px;
  width: 100%;
  cursor: pointer;
`;

const Label = styled.span`
  color: #DEF7E9;
  letter-spacing: 0.44px;
  line-height: 24px;
  font-size: 16px;
  font-weight: 400;
`;

const Title = styled.span`
  color: #FFFFFF;
  letter-spacing: -0.15px;
  line-height: 28px;
  font-size: 20px;
  font-weight: 500;
`;

interface ISelector {
  onClick: () => any;
}
const BackComponent: FC<ISelector> = (props) => {
  const { t: translation } = useTranslation();

  return (
    <>
      <Container onClick={props.onClick}>
        <ChevronLeftIcon style={{ color: '#FFFFFF' }} />

        <Title>{translation('interview.firstScreen.sixAnswer.title')}</Title>
        <Label>{translation('interview.firstScreen.sixAnswer.subtitle')}</Label>
      </Container>
    </>
  );
};

export const Back = memo(BackComponent);
