import React, { FC, memo } from 'react';
import { useTranslation } from 'react-i18next';

import { Subtitle, Title, SelectorContainer, Selector } from '../StyledComponents';

interface IApi {
  onSubmit: (isEnabled: boolean) => any;
}
const Api: FC<IApi> = (props) => {
  const { t: translation } = useTranslation();

  return (
    <>
      <Subtitle>
        {translation('interview.fourScreen.title')}
      </Subtitle>
      <Title>
        {translation('interview.fourScreen.subtitle')}
      </Title>

      <SelectorContainer>
        <Selector title={translation('interview.fourScreen.firstAnswer.title')} onClick={() => props.onSubmit(false)} />
        <Selector title={translation('interview.fourScreen.secondAnswer.title')} onClick={() => props.onSubmit(true)} />
      </SelectorContainer>
    </>
  );
};

export default memo(Api);
