import React, { FC, memo, useEffect, useState } from 'react';
import { history } from '../../services'
import { PageWrapper } from './StyledComponents';
import Helmet from 'react-helmet';
import Purposes from './components/Purposes';
import Accounts from './components/Accounts';
import Api from './components/API';
import From from './components/From';
import Team from './components/Team';
import { sendQuizInfo, IQuiz } from './api';
import { useUser } from '../../hooks';
import { shwoQuizPage } from './api';

const isElectron = !!window.require;

const answers: IQuiz = {}

type TQuizStage = 'accounts'|'api'|'from'|'purposes'|'team';

const Quiz: FC = () => {
  const [stage, setStage] = useState<TQuizStage>('purposes');
  const user = useUser();

  const onPageInit = async () => {
    user.updateUser({
      targetPageShown: true,
    });
    shwoQuizPage();
  };

  useEffect(() => {
    onPageInit();
  }, []);

  const onQuizStageComplete = (quizStage: TQuizStage, value: any) => {
    switch (quizStage) {
      case 'accounts':
        answers.profiles = value;
        setStage('api');
        break;

      case 'api':
        answers.api = value;
        setStage('from');
        break;

      case 'from':
        answers.from = value;
        onQuizEnd();
        break;

      case 'purposes':
        answers.survey = value;
        setStage('team');
        break;

      case 'team':
        answers.teamMembers = value;
        setStage('accounts');
        break;
    }

    sendQuizInfo(answers);
  }

  const onQuizEnd = () => {

    if (isElectron) {
      history.replace('/');
      return;
    }

    history.replace('/download');
  }

  const stages = {
    accounts: <Accounts onSubmit={(count) => onQuizStageComplete('accounts', count)} />,
    api: <Api onSubmit={(isEnabled) => onQuizStageComplete('api', isEnabled)} />,
    from: <From onSubmit={(text) => onQuizStageComplete('from', text) } />,
    purposes: <Purposes onSubmit={(text) => onQuizStageComplete('purposes', text)} />,
    team: <Team onSubmit={(count) => onQuizStageComplete('team', count)} />,
  };

  return (
    <PageWrapper>
      {stages[stage]}

      <Helmet>
        <title>Set up your account - GoLogin</title>
      </Helmet>
    </PageWrapper>
  );
}

export default memo(Quiz);
