import React, { FC, memo, useState } from 'react';
import { useTranslation } from 'react-i18next';

import { Subtitle, Title, SelectorContainer, Selector } from '../StyledComponents';
import ManualTextInputForm from './ManualTextInputForm';

interface IFrom {
  onSubmit: (text: string) => any;
}
const From: FC<IFrom> = (props) => {
  const [isManualFormOpened, setIsManualFormOpened] = useState(false);

  const { t: translation } = useTranslation();

  const answers = [
    { label: translation('interview.fiveScreen.firstAnswer.title'), key: 'Forum Comments' },
    { label: translation('interview.fiveScreen.secondAnswer.title'), key: 'Internet Article' },
    { label: translation('interview.fiveScreen.thirdAnswer.title'), key: 'YouTube Video' },
    { label: translation('interview.fiveScreen.fourAnswer.title'), key: 'Recommended In Chat' },
    { label: translation('interview.fiveScreen.fiveAnswer.title'), key: 'Suggested by Friend' },
  ];

  const renderContent = () => {
    if (isManualFormOpened) {
      return (
        <ManualTextInputForm
          placeholder={translation('interview.fiveScreen.subtitle')}
          onSubmit={(text) => props.onSubmit(text)}
          return={() => setIsManualFormOpened(false)}
        />
      );
    }

    return (
      <>
        {answers.map((answer) => (
          <Selector key={answer.key} title={answer.label} onClick={() => props.onSubmit(answer.key)} />
        ))}
        <Selector title={translation('interview.fiveScreen.sixAnswer.title')} onClick={() => setIsManualFormOpened(true)} />
      </>
    );
  };

  return (
    <>
      <Subtitle>
        {translation('interview.fiveScreen.title')}
      </Subtitle>
      <Title>
        {translation('interview.fiveScreen.subtitle')}
      </Title>

      <SelectorContainer>
        {renderContent()}
      </SelectorContainer>
    </>
  );
};

export default memo(From);
