import React, { FC, memo } from 'react';
import styled from '@emotion/styled';
import CircularProgress from '@material-ui/core/CircularProgress';

interface IStyledButton {
  loading?: boolean;
  width?: number;
}
export const StyledButton = styled.button<IStyledButton>`
  padding: 19px 29px;
  color: #000000;
  font: 300 18px/21.6px 'Graphik LC', Roboto;
  margin: 22px auto 0 auto;
  background: #FFE164;
  border-radius: 60px;
  outline: none;
  border: none;
  cursor: pointer;
  transition: all linear .3s;
  
  width: ${props => `${props.width}px` || 'auto'};

  ${(props) => {
    if (!props.loading) {
      return `
        :hover {
          color: #FFE164;
          background: #000000;
        }
      `;
    }
  }}
`;

interface ISubmitButton extends React.ButtonHTMLAttributes<HTMLButtonElement> {
  loading?: boolean;
  width?: number;
}
const SButton: FC<ISubmitButton> = (props) => {

  if (props.loading) {
    return (
      <StyledButton {...props}>
        <CircularProgress value={99} disableShrink={true} size={14} color='inherit' />
      </StyledButton>
    );
  }

  return (
    <StyledButton {...props}>{props.children}</StyledButton>
  );
}

export const SubmitButton = memo(SButton);
