import styled from '@emotion/styled';
import { Input } from 'antd';

export const TextArea = styled(Input.TextArea)`
  font-size: 15px;
  margin-top: 5px;
  padding: 13px;
  letter-spacing: 0.2px;
  max-height: 113px;

  background-color: #FFFFFF;
  border: 1px solid #D9D9D9;
  color: #000000A6;
  ::placeholder {
    color: #BFBFBF;
  }
`;
